body {
	min-width: 320px;
	background: #fff;
	font-family: $gotham500;
	font-weight: 500;
 }	// overflow: hidden

h5 {
	margin: 0;
	padding: 0;
	font-size: 40px;
	color: $blue; }

h2 {
	font-size: 22px;
	color: $blue; }

h3 {
	font-size: 16px;
	color: $blue;
	padding-bottom: 5px; }

.pagewrap {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100vh; }

.main {
	height: 100%;
	background-color: $color_main_bg; }

.header__color__bg {
	background: linear-gradient(to bottom, #dce4ea, #dbe3e9); }

