.news-btn {
	position: absolute;
	display: block;
	right: 15px;
	bottom: 10px;
	background: none;
	color: $blue;
	font-family: $gotham500;
	font-weight: 500;
	font-size: 12px;
	transition: background 0.3s, color 0.3s;
	&:hover {
		color: $orange; } }

.news__toggle__btn {
	display: none; }

.news__toggle {
	display: flex;
	align-items: center;
	justify-content: space-between;
	background: none;
	width: 27px;
	height: 20px;
	cursor: pointer;
	margin-left: 4px;

	.toggle-dots {
		display: block;
		content: '';
		width: 5px;
		height: 5px;
		border-radius: 50%;
		background-color: $blue_light; } }


.news {
	height: 100%;
	background-color: $color_main_bg;
	&__container {
		font-size: 16px;
		padding: 15px 115px 0; } }

.news__title {
	display: flex;
	flex-direction: column; }

.news__title__step {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 25px;
	font-size: 12px; }

.dots {
	display: block;
	content: '';
	width: 3px;
	height: 3px;
	border: 50%;
	background-color: $blue_white;
	margin: 0 10px; }

.newses {
	color: $blue_white; }

.news__menu {
	text-align: right;
	font-size: 12px;
	color: $blue_light; }

.news__row__first {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	font-size: 12px;
	font-weight: 500;
	color: $blue;

	.news__left__block {
		position: relative;
		flex: 5 1 22%;
		background-color: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; } }


	.news__center__block {
		flex: 1 1 55%;
		margin: 0 20px;
		position: relative;
		background-color: #fff;
		img {
			width: 100%;
			height: 100%; }
		span {
			display: block;
			position: absolute;
			bottom: 50px;
			left: 0;
			padding: 0 15px; } }

	.gradient {
		position: absolute;
		width: 100%;
		height: 100%;
		background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 20%, rgba(0, 0, 0, 0.1) 55%); }

	.news__right__block {
		position: relative;
		flex: 2 1 22%;
		background: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; } } }

.news__row__second {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	font-size: 12px;
	font-weight: 500;
	color: $blue;

	.news__block {
		position: relative;
		flex: 1 1 25%;
		background-color: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; }

		&:first-child {
			margin-right: 20px; }

		&:nth-child(3) {
			margin: 0 20px;
			img {
				height: 100%; }
			span {
				display: block;
				position: absolute;
				bottom: 20px;
				left: 0;
				padding: 0 15px; } }

		.gradient {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 25%, rgba(0, 0, 0, 0.1) 65%); } } }

.news__row__third {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	font-size: 12px;
	font-weight: 500;
	color: $blue;

	.news__left__block {
		flex: 5 1 50%;
		margin-right: 20px;

		.news__block {
			height: 100%;
			position: relative;
			background-color: #fff;
			img {
				width: 100%;
				height: 100%; }
			span {
				display: block;
				position: absolute;
				bottom: 50px;
				left: 0;
				padding: 0 15px; }
			&:first-child {
				display: none; } }

		.gradient {
			position: absolute;
			width: 100%;
			height: 100%;
			background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 20%, rgba(0, 0, 0, 0.1) 55%); } }

	.news__right__block {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex: 1 1 50%;

		.news__block {
			position: relative;
			background-color: #fff;
			width: 100%;
			height: 30%;
			img {
				width: 35%; }
			span {
				display: block;
				padding: 10px 15px 0;
				margin-bottom: 30px; } }

		.news__block {
			display: flex; } } }

.news__row__four {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	font-size: 12px;
	font-weight: 500;
	color: $blue;

	.news__left__block {
		position: relative;
		flex: 3 1 22%;
		background-color: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; } }


	.news__center__block {
		flex: 1 1 55%;
		margin: 0 20px;
		position: relative;
		background-color: #fff;
		img {
			width: 100%;
			height: 100%; }
		span {
			display: block;
			position: absolute;
			bottom: 50px;
			left: 0;
			padding: 0 15px; } }

	.gradient {
		position: absolute;
		width: 100%;
		height: 100%;
		background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 20%, rgba(0, 0, 0, 0.1) 55%); }

	.news__right__block {
		position: relative;
		flex: 1 1 22%;
		background: #fff;
		img {
			width: 100%; }
		span {
			display: block;
			padding: 8px 20px 0;
			margin-bottom: 40px; } } }

.button__loading {
	display: flex;
	justify-content: center;

	.loading__more {
		text-align: center;
		margin: 45px 0 40px;
		padding: 3px 15px;
		background: none;
		border: 1px solid $blue;
		border-radius: 15px;
		color: $blue;
		transition: background 0.3s, border 0.3s, color 0.3s;
		&:hover {
			border: 1px solid $blue;
			background: $blue;
			color: #fff; } } }
